.single {
  display: flex;
  width: 100%;

  .singleContainer {
    flex: 6;

    .top {
      display: flex;
      padding: 20px;
      gap: 20px;

      .left {
        position: relative;
        flex: 1;
        padding: 20px;
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      }

      .editButton {
        position: absolute;
        top: 0;
        right: 0;
        padding: 5px;
        font-size: 12px;
        color: #7451f8;
        background-color: #7551f818;
        cursor: pointer;
        border-radius: 0px 0px 0px 5px;
      }

      .right {
        flex: 2;
      }

      .item {
        display: flex;
        gap: 20px;

        .itemImg {
          height: 100px;
          width: 100px;
          border-radius: 50%;
          object-fit: cover;
        }

        .details {
          .itemTitle {
            margin-bottom: 10px;
            color: #555;
          }
          .detailItem {
            margin-bottom: 10px;
            font-size: 14px;

            .itemKey {
              font-weight: bold;
              color: gray;
              margin-right: 5px;
            }

            .itemValue {
              font-weight: 300;
            }
          }
        }
      }
    }

    .bottom {
        padding: 20px;
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        margin: 20px;
      }
    
      .title {
        font-size: 16px;
        color: lightgray;
        margin-bottom: 10px 20px;
      }
  }
}
